import Menu from '../Utils/Menu'
import './Navbar.css'
const Navbar = (props)=>{
    return(
        <div className="navbar">
            <div className="navLogo">
                <img src="logo192.png" alt="QMS SOLUTIONS"/>
                <div className="description">
                    <div className="title font-asap-bold">{props.title}</div>
                    <div className="subtitle oswald" >{props.subtitle}</div>
                </div>
            </div>
            <ul className="nav">
                {
                    Menu.default.map((data) => {
                        return(
                            <li className="navLink montserrat" key={data.key}><a href={data.href}>{data.item}</a></li>
                        )
                    })
                }
            </ul>
        </div>
    )
}
export default Navbar;