import parser from 'html-react-parser';
import React from 'react'
import './Slideshow.css'

class Slideshow extends React.Component {
    constructor(props){
        super(props)
    }
    componentDidMount(){
        this.showSlides(this.slideIndex = 1)
    }
    plusSlides(n){
        if(n.target.className==="next")
            this.showSlides(this.slideIndex += 1);
        else
            this.showSlides(this.slideIndex -= 1);
    }

    currentSlide(n){
        this.showSlides(this.slideIndex = parseInt(n.target.id));
    }
    showSlides(n){
        var i;
        var slides = document.getElementsByClassName("mySlides");
        var dots = document.getElementsByClassName("dot");

        console.log(this.slideIndex, "showslide")
        if (n > slides.length) {
            this.slideIndex = 1
        }
        if (n < 1) {
            this.slideIndex = slides.length
        }
        for (i = 0; i < slides.length; i++) {
            slides[i].style.display = "none";
        }
            
        for (i = 0; i < dots.length; i++) {
            dots[i].className = dots[i].className.replace(" active", "");
        }
        
        try {
            if(slides !== undefined)
                slides[this.slideIndex-1].style.display = "block";
            if(dots !== undefined)
                dots[this.slideIndex-1].className += " active";    
        } catch (error) {
            console.log(error.message   )   
        }
        
    }
    render(){
        return(
            <section>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-sm-12">
                        <div className="slideshow-container">
                        {
                            this.props.slides.map((data) =>{
                                return(
                                    <div className="mySlides sfade" key={data.key}>
                                        <h3 className="title">{parser(data.title)}</h3>
                                        <div className="description">{parser(data.description)}</div>
                                    </div>
                                );
                            })
                        }
                        <a className="prev" onClick={this.plusSlides.bind(this)}>&#10094;</a>
                        <a className="next" onClick={this.plusSlides.bind(this)}>&#10095;</a>
                    </div>
                    <div style={{textAlign:"center"}}>
                        {
                            this.props.slides.map((data) => {
                                return(
                                    <span className="dot" id={data.key} onClick={this.currentSlide.bind(this)} key={data.key}></span>
                                );
                            })
                        }
                    </div>
                        </div>
                    </div>
                    
                </div>
            </section>
        );
    }
}

export default Slideshow;