const FormGroup = (props) => {
    return(
        <div className="row">
            <div className="col-sm-12">
                <div className="form-group">
                    {
                        props.type === "textarea" ? 
                           <textarea id={props.id} name={props.id} rows={props.rows} className="form-control"></textarea> 
                         : <input id={props.id} name={props.id} type={props.type ? props.type : "text"} className="form-control" /> 
                    }
                    <label htmlFor={props.id}>{props.label}</label>
                </div>
            </div>
        </div>
    );
}
export default FormGroup;